import React from 'react';
import {
  Modal,
  ModalBody,
  Button,
  Form,
} from 'reactstrap';

import {EliminarCliente} from '../../Dusk/DashboardDusk';
import { useHistory } from 'react-router-dom';




const ConfirmModal = ({ modalOpen, toggleModal,customerid }) => {

  let history = useHistory();


  const handleSubmit = (e) => {

    EliminarCliente(customerid)
    history.push('/app/pages/customers/all')
    }




  return (
    <Modal
      isOpen={modalOpen}
      toggle={toggleModal}
      size="sm"
    >
      <ModalBody>
      <Form className="form-container" onSubmit={handleSubmit}>
        <p>
          <i 
            className="simple-icon-question" 
            style={{ color: 'red', marginRight: '5px' }}
          />
          ¿Estás seguro de eliminar?
        </p>


        <div className="float-right">
          <Button 
            outline
            color="dark"
            size="sm"
            onClick={toggleModal}
          >
            Cancelar
          </Button>

          <Button 
            color="primary"
            size="sm"
            onClick={toggleModal}
            type="submit"
          >
            Sí, ¡adelante!
          </Button>
        </div>
        </Form>
      </ModalBody>
    </Modal>
  );
};

export default ConfirmModal;
